.drawer {
  display: block;
  position: absolute;
  z-index: 350;
  transition: all 0.5s cubic-bezier(0.1, 0.7, 0.1, 1);
  will-change: translate;
  visibility: hidden;
  height: calc(100vh - 55px); }
  .drawer.is-open {
    visibility: visible; }
    .drawer.is-open .drawer__mask {
      opacity: 1;
      visibility: visible; }
    .drawer.is-open .drawer__content {
      z-index: 360;
      opacity: 1;
      visibility: visible;
      overflow: hidden; }
  .drawer hr {
    height: 1px;
    width: 100%;
    margin: 8px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.01); }
  .drawer--dark {
    background-color: #242326;
    background-color: var(--px-drawer-background-color, #242326);
    color: #FFFFFF; }
    .drawer--dark drawer__button {
      color: #FFFFFF; }
  .drawer--left {
    left: 0;
    top: 0;
    bottom: 0;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
    width: 30%;
    height: 100%;
    width: var(--px-drawer-width, 30%);
    height: var(--px-drawer-height, 100%); }
    .drawer--left.is-open {
      -webkit-transform: translateX(0);
      transform: translateX(0); }
    @media only screen and (max-device-width: 640px), only screen and (max-device-width: 667px), only screen and (max-width: 480px) {
      .drawer--left {
        width: 100%;
        width: var(--px-drawer-width, 100%); } }
    @media (min-width: 1200px) {
      .drawer--left {
        max-width: 330px;
        width: 25%;
        width: var(--px-drawer-width, 30%); } }
    @media (min-width: 767px) {
      .drawer--left {
        width: 35%;
        max-width: 330px;
        width: var(--px-drawer-width, 35%); } }
  .drawer--right {
    top: 0;
    bottom: 0;
    right: 0;
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
    height: var(--px-drawer-height, 100%); }
    .drawer--right.is-open {
      -webkit-transform: translateX(0);
      transform: translateX(0); }
    @media only screen and (max-device-width: 640px), only screen and (max-device-width: 667px), only screen and (max-width: 480px) {
      .drawer--right {
        width: 100%;
        width: var(--px-drawer-width, 100%); } }
    @media (min-width: 767px) {
      .drawer--right {
        width: 35%;
        max-width: 330px;
        width: var(--px-drawer-width, 35%); } }
    @media (min-width: 1200px) {
      .drawer--right {
        max-width: 330px;
        width: 25%;
        width: var(--px-drawer-width, 30%); } }
  .drawer--top {
    top: 0;
    right: 0;
    left: 0;
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
    width: var(--px-drawer-width, 100%); }
    .drawer--top.is-open {
      -webkit-transform: translateY(0);
      transform: translateY(0); }
    @media (min-width: 1200px) {
      .drawer--top {
        height: var(--px-drawer-height, 50%); } }
    @media only screen and (max-device-width: 640px), only screen and (max-device-width: 667px), only screen and (max-width: 480px) {
      .drawer--top {
        height: var(--px-drawer-height, 100%); } }
    @media (min-width: 767px) {
      .drawer--top {
        height: var(--px-drawer-height, 50%); } }
  .drawer--bottom {
    bottom: 0;
    right: 0;
    left: 0;
    width: var(--px-drawer-width, 100%);
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.4); }
    .drawer--bottom.is-open {
      -webkit-transform: translateY(0);
      transform: translateY(0); }
    @media only screen and (max-device-width: 640px), only screen and (max-device-width: 667px), only screen and (max-width: 480px) {
      .drawer--bottom {
        right: 0;
        left: 0;
        width: 100%;
        height: 100%;
        height: var(--px-drawer-height, 100%); } }
    @media (min-width: 767px) {
      .drawer--bottom {
        right: 25%;
        left: 25%;
        width: 50%;
        height: 35%;
        height: var(--px-drawer-height, 35%); } }
    @media (min-width: 1200px) {
      .drawer--bottom {
        width: 50%;
        height: 25%;
        height: var(--px-drawer-height, 25%);
        right: 25%;
        left: 25%; } }
    .drawer--bottom--full-width {
      width: 100% !important;
      left: 0;
      right: 0; }
  .drawer--mini {
    transition: width .3s ease;
    position: static;
    visibility: visible;
    /* margin-left: -25px; */
    float: left;
    width: 44px;
    -webkit-transform: translateX(0) !important;
    transform: translateX(0) !important; }
    .drawer--mini .viewport:parent {
      margin-left: -20px; }
    .drawer--mini.is-open {
      width: 20%; }
      @media (min-width: 1200px) {
        .drawer--mini.is-open {
          width: 20%; } }
      @media only screen and (max-device-width: 640px), only screen and (max-device-width: 667px), only screen and (max-width: 480px) {
        .drawer--mini.is-open {
          width: 100%; } }
      @media (min-width: 767px) {
        .drawer--mini.is-open {
          width: 100%; } }
      .drawer--mini.is-open .table-row__content__title {
        width: auto; }
    .drawer--mini:not(.is-open) .table-row--nav-right:before {
      content: ''; }
    .drawer--mini:not(.is-open) .table-row--nav-right .table-row__content__title {
      width: 0;
      overflow: hidden; }
  .drawer--temporary {
    transition: all 0.5s cubic-bezier(0.1, 0.7, 0.1, 1);
    position: absolute; }
  .drawer--persistent {
    transition: width .5s ease-in-out;
    position: static;
    visibility: visible;
    float: left;
    width: 20%;
    margin-top: -10px;
    margin-left: -25px;
    -webkit-transform: translateX(0) !important;
    transform: translateX(0) !important; }
    @media (min-width: 1200px) {
      .drawer--persistent {
        margin-top: 0; } }
    @media only screen and (max-device-width: 640px), only screen and (max-device-width: 667px), only screen and (max-width: 480px) {
      .drawer--persistent {
        margin-left: 0; } }
    @media (min-width: 767px) {
      .drawer--persistent {
        margin-top: 0;
        margin-left: 0; } }
  .drawer--fixed {
    position: fixed; }
  .drawer__content {
    padding: 0;
    width: 100%;
    height: 100%;
    display: block;
    overflow: hidden;
    position: relative;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
    .drawer__content--grid .grid__item {
      padding-left: 24px;
      padding-right: 24px; }
      .drawer__content--grid .grid__item:hover {
        background: rgba(0, 0, 0, 0.2); }
    .drawer__content--grid a {
      text-align: center;
      padding: 5px 12px;
      text-decoration: none;
      display: block; }
      .drawer__content--grid a i {
        height: 48px;
        width: 48px;
        font-size: 48px;
        display: block;
        margin-bottom: 6px; }
